import React, { PureComponent, Suspense } from "react";
import { PostTrimContent, themeImpressionTracking, IS_THEME } from '../../../../utils'
import { isTagEmbed, suiteTrack } from "../../../../actions/api";
import AuthorDetails from "./AuthorDetails"
import Media from "./Media"
import PostContent from '../../Elements/PostContent/PostContent'
import ShareOnHover from "../../Elements/ShareOnHover/ShareOnHover"
import CTAButton from "../../Elements/CTA/CtaButton"
import Rating from "../../Elements/Rating/Rating"
import SocialAction from "../../Elements/SocialAction/SocialAction"
import Network from "../../Elements/Network/Network"
const trim_content_length = !isTagEmbed ? 200 : null;

class Card extends PureComponent {
  constructor(props) {
    super(props);
    this.postTrack = React.createRef();
  }
  componentDidMount() {
    const { itemData, itemIndex, onClickPopUpSlider } = this.props
    if (suiteTrack) themeImpressionTracking(this.postTrack)
    if (itemData.isPopUp) {
      onClickPopUpSlider(itemIndex, itemData)
    }
    try {
      document.querySelector(`.tb_mc_post_cta_btn_${itemData.id}`)?.addEventListener('keydown', function (event) {
        if (event.key === 'Enter' || event.key === ' ') {
          event.preventDefault();
          this.click(); // Triggers the existing onClick functionality
        }
      });
      document.querySelector(`.tb_mc_post_in${itemData.id}`)?.addEventListener('keydown', function (event) {
        if (event.key === 'Enter' || event.key === ' ') {
          event.preventDefault();
          this.click(); // Triggers the existing onClick functionality
        }
      });
    }
    catch (e) {
      console.log(e)
    }

  }
  render() {
    const { itemData, personalization, adjustWidth, ThemeRule, clickToShowPopUp, itemIndex, wallID, ownerId, onClickToCTA, wall } = this.props;
    const mediaType = !(isTagEmbed && IS_THEME(52)) && itemData.type !== 1 ? true : false;
    const rating = (itemData.rating > 0) ? true : false;
    const contentClass = (itemData.rating > 0) ? 'tb_mc_rating_content' : 'tb_mc_content'
    const textDecorate = (itemData.type === 1 && itemData.content && String(itemData.content).length > 0 && personalization.textDecorate) ? 'tb_mc_text_decoration tb_mc_text_post' : '';
    const ctaStatus = (itemData.cta && Object.keys(itemData.cta).length > 0 && itemData.cta.status) ? true : false;
    const postTextContent = personalization.trimcontent ? PostTrimContent(itemData.content, trim_content_length) : itemData.content
    return <div className={`tb_mc_post_wrapper`} ref={this.postTrack} data-id={itemData.id} style={{ width: `${itemData.highlight == 1 ? adjustWidth * 2 : adjustWidth}%`, padding: personalization.padding / 2 }} tb-network={itemData.network.icon} post-id={itemData.referenceId ? itemData.referenceId : itemData.id} feed-id={itemData.feedId} tabIndex="0" area-label={postTextContent} role="article">
      <div className={`tb_mc_post_wrap_in ${isTagEmbed && IS_THEME(52) ? 'te_mc_post_wrap_in' : ''}`}>
        <div className={`tb_mc_post_in tb_mc_post_in${itemData.id}`} onClick={clickToShowPopUp(itemIndex, itemData)} role="button" aria-roledescription="Click to open pop up" tabIndex={"0"}>
          {isTagEmbed && IS_THEME(20) ? <div className="tb_mc_author_container">
            <AuthorDetails ownerId={ownerId} mediaType={mediaType} postTime={itemData.createdAt} author={itemData.author} network={itemData.network} font={ThemeRule} personalization={personalization} ThemeRule={ThemeRule} />
          </div> : null}
          <div className="tb_mc_post_media_wrapp" role="button" tabIndex="0" aria-label="Pop Up Button">
            {itemData.share.status ? <ShareOnHover share={itemData.share} shareClass={'tb_mc_share_container'} item={itemData} wallID={wallID} ownerId={ownerId} /> : null}
            {mediaType ? <Media itemData={itemData} wallID={wallID} ownerId={ownerId} ThemeRule={ThemeRule} wall={wall} /> : null}
          </div>
          <div className="tb_mc_contant_wrapper">
            {!isTagEmbed && !mediaType && IS_THEME(20) ? <div className="tb_mc_social_top_"> <Network networkClass={'tb_mc_social_center_ico'} network={itemData.network} isDefault={ThemeRule.iconType === 1 ? true : false} ThemeRule={ThemeRule} /> </div> : null}
            <div className={`tb_mc_content_wrap ${IS_THEME(52) ? 'tb_mc_content_wrap_fixed' : ''}`} role="article" aria-roledescription="Post content wrapper" aria-label="Post content wrapper includes rating ,content and CTA Button">
              {rating ? <div className={`tb_mc_rating__ ${itemData.network.id === 29 ? `tb_mc_onsite_rating__` : ``}`}><Rating rating={itemData.rating} network={itemData.network} /> </div> : null}
              {ctaStatus && !isTagEmbed ? <div className="tb_mc_post_cta"><CTAButton ctaClass={`tb_mc_post_cta_btn tb_mc_post_cta_btn_${itemData.id}`} cta={itemData.cta} item={itemData} onClickToCTA={onClickToCTA} /> </div> : null}

              {(itemData.content && String(itemData.content).length > 0) && (!ThemeRule.hideContent || !mediaType) ? <PostContent item={itemData} contentClass={`${contentClass} ${textDecorate}`} content={postTextContent} font={ThemeRule} ThemeRule={ThemeRule} personalization={personalization} contentTitle={itemData.contentTitle} /> : null}
            </div>
            {ctaStatus && isTagEmbed ? <div className="tb_mc_post_cta"><CTAButton ctaClass={`tb_mc_post_cta_btn tb_mc_post_cta_btn_${itemData.id}`} cta={itemData.cta} item={itemData} onClickToCTA={onClickToCTA} /> </div> : null}

            {!isTagEmbed || IS_THEME(52) ? <AuthorDetails ownerId={ownerId} mediaType={mediaType} postTime={itemData.createdAt} author={itemData.author} network={itemData.network} font={ThemeRule} personalization={personalization} ThemeRule={ThemeRule} /> : null}
          </div>
          {ThemeRule.socialAction && !IS_THEME(52) ? <SocialAction itemData={itemData} ThemeRule={ThemeRule} ThemeID={personalization.widgetTheme} actionClass={`tb_mc_social_action__`} /> : null}
        </div>
      </div>
    </div>
  }
}
export default Card;