import { isTagEmbed, isTagbox } from "../../../../actions/api";
import MediaIcon from "../../Elements/MediaIcon/MediaIcon";
import Image from "../../Elements/Media/Image";
import Video from "../../Elements/Media/Video";
const Media = ({ itemData, wallID, ownerId, wall }) => {
  const isVideo = (itemData.type === 3 || itemData.type === 5)
  const isTagEmbedStatus = wall?.User?.userDatabaseType == "tagembed"
  const autoPlayTagEmbed = isTagEmbedStatus && isVideo && !wall.Personalization.mobilePopup && !wall.Personalization.postFeatured ? true : false
  const autoPlay = isVideo && itemData.network.id !== 28 && wall.ThemeRule.autoPlay == 1 ? true : false
  const size = isTagEmbedStatus || isTagbox ? wall.ThemeRule.aspectImageRatio ? wall.ThemeRule.aspectImageRatio : false : false

  return <div className="tb_mc_media_wrap" role="button" tabIndex="0" aria-label="Pop Up Button" >
    {!autoPlayTagEmbed && !autoPlay ? <MediaIcon itemData={itemData} IconClass="tb_mc_media_icon" show={true} isCenter={isTagEmbedStatus} /> : null}
    {autoPlay || autoPlayTagEmbed ?
      <Video VideoClass="tb_mc_video" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={size} isCover={true} controls={false} autoPlay={true} muted={true} />
      :
      <Image ImageClass="tb_mc_image" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={size} ThemeRule={wall.ThemeRule} />
    }
  </div>
}
export default Media