import Author from "../../Elements/Author/Author";
import Time from "../../Elements/Time/Time";
import Network from "../../Elements/Network/Network";
import { isTagbox, isTagEmbed } from "../../../../actions/api";
import { IS_THEME } from "../../../../utils";
const AuthorDetails = ({ author, personalization, postTime, network, ThemeRule, font, mediaType, ownerId }) => {
  const postAuthor = (personalization.postAuthor && !author.isInstaUser) ? true : false;
  const authorSeprator = (personalization.postAuthor && personalization.postTime && !author.isInstaUser) ? true : false;
  return (
    <div className="tb_mc_author_wrapper" aria-label="Author details" aria-roledescription="post author details">
      <div className="tb_mc_author">
        {postAuthor ? <Author network={network} author={author} authorClass={'tb_mc_author_profile'} key={author.username} /> : ''}
        <div className="tb_mc_author_info">
          {postAuthor ? <div className="tb_mc_authorname" aria-label={author.name}>{author.name}</div> : ''}
          <div className="tb_mc_post_info">
            {postAuthor ? <div className="tb_mc_username" aria-label={author?.username || ""}>{author.username ? author.username.length > 0 ? `@${author.username}` : `` : ''}</div> : ''}
            {authorSeprator ? <div className="tb_mc_seprator"> </div> : ''}
            {personalization.postTime ? <Time postTime={postTime} timeClass={`tb_mc_time tb-cTBfont-${ThemeRule.font_varient}`} /> : ''}
          </div>
        </div>
      </div>
      {mediaType && network.id || isTagEmbed || (isTagbox && IS_THEME(79)) ? <div className="tb_mc_social_">
        <Network networkClass={'tb_mc_social_ico'} network={network} isDefault={ThemeRule.iconType === 1 ? true : false} ThemeRule={ThemeRule} />
      </div> : null}
    </div>
  );
}
export default AuthorDetails